import { Component, EventEmitter, Output } from "@angular/core";

import { SentenceService } from '../_services/sentence.service';
import { ClickService } from '../_services/click.service';
import { trigger, style, transition, animate, keyframes ,state} from "@angular/animations";

@Component({
    moduleId: module.id,
    templateUrl: 'archive-menu.component.html',
    styleUrls: ['./archive-menu.component.css'],
    selector: "archive-menu",
    animations: [
      trigger('flyInOut', [
        state('in', style({opacity: 1, transform: 'translateX(0)'})),
        transition('* => void', [
          animate('0.2s 0.1s ease-out', style({
            opacity: 0,
            transform: 'translateX(100%)'
          }))
        ])
    ])
    ]

})

export class ArchivemenuComponent {
    archivedSentences: string[] = [];
    constructor(
        private sentenceService: SentenceService,
        private clickService: ClickService
    ){
        this.sentenceService.emptyArchive$.subscribe(value => {
            if(value == true){
                this.archivedSentences = [];
            }
        })
        this.sentenceService.sentence$.subscribe(data => {
            this.archivedSentences.unshift(data);
        });
        this.clickService.undoClick$.subscribe(data => {
            this.archivedSentences.shift();
        })
    }
    deleteSentence(text:string){
        for(var i = this.archivedSentences.length; i >= 0; i--){
            if(this.archivedSentences[i] === text){
                this.archivedSentences.splice(i, 1);
            }
        }
    }
    @Output() offClick = new EventEmitter();

    closeArchiveSideMenu(){
        this.offClick.emit("close");
    }
    saveSentences(){
        this.clickService.showSaveFileModal();
    }
}
